@import "../../styles/variables";

.vrTaskContainer {
  position: relative;
  min-height: 100vh;
}

.requestButton {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.5);
  border: none;
  border-radius: 6px;
  color: $color-secondary-text;
  font-size: 40px;
  padding: $space-2;
  letter-spacing: 3px;
}

.closeButtonIcon {
  width: 14px;
  height: 14px;
  fill: $color-secondary-text;
}
